*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

p {
  margin: 0;
}

cite {
  font-style: normal;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

@font-face {
  font-family: "Muller";
  src: url("../fonts/MullerLight.woff2") format("woff2"), url("../fonts/MullerLight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Muller";
  src: url("../fonts/MullerRegular.woff2") format("woff2"), url("../fonts/MullerRegular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Muller";
  src: url("../fonts/MullerMedium.woff2") format("woff2"), url("../fonts/MullerMedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Muller";
  src: url("../fonts/MullerBlack.woff2") format("woff2"), url("../fonts/MullerBlack.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  max-width: 1200px;
  width: 100%;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.row.equal-height > [class^=col-] {
  display: flex;
}

.col-12 {
  flex-basis: 100%;
  max-width: 100%;
}

.col-11 {
  flex-basis: 91.6667%;
  max-width: 91.6667%;
}

.col-10 {
  flex-basis: 83.333%;
  max-width: 83.3333%;
}

.col-9 {
  flex-basis: 75%;
  max-width: 75%;
}

.col-8 {
  flex-basis: 66.6667%;
  max-width: 66.6667%;
}

.col-7 {
  flex-basis: 58.3333%;
  max-width: 58.3333%;
}

.col-6 {
  flex-basis: 50%;
  max-width: 50%;
}

.col-5 {
  flex-basis: 41.6667%;
  max-width: 41.6667%;
}

.col-4 {
  flex-basis: 33.3333%;
  max-width: 33.3333%;
}

.col-3 {
  flex-basis: 25%;
  max-width: 25%;
}

.col-2 {
  flex-basis: 16.6666%;
  max-width: 16.6666%;
}

.col-1 {
  flex-basis: 8.3333%;
  max-width: 8.3333%;
}

.col-1,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-10,
.col-11,
.col-12 {
  flex-grow: 1;
  padding-right: 15px;
  padding-left: 15px;
  position: relative;
  width: 100%;
  min-height: 1px;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.hidden {
  visibility: hidden;
  display: none !important;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-center {
  text-align: center;
}

.text-just {
  text-align: justify;
}

.flex {
  display: flex;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
  font-weight: 700;
}

.h1 {
  font-size: 60px;
  margin: 0;
}

.h2 {
  font-size: 48px;
}

.h3 {
  font-size: 36px;
}

.h4 {
  font-size: 26px;
}

.h5 {
  font-size: 22px;
}

.h6 {
  font-size: 18px;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  line-height: 100%;
}

body {
  background-color: #34afaa;
  font-family: sans-serif;
  font-size: 16px;
  line-height: 100%;
  color: #000;
}

#test {
  padding: 2em 3em;
  text-transform: uppercase;
  font-family: sans-serif;
  font-size: 3em;
  line-height: 1.5;
  font-weight: 700;
  color: #fff;
}

#test .inverted {
  color: #18252b;
}

#test .awesome {
  text-decoration: underline;
}

@media screen and (max-width: 767px) {
  .row {
    margin-right: -5px;
    margin-left: -5px;
  }

  .col-1,
  .col-2,
  .col-3,
  .col-4,
  .col-5,
  .col-6,
  .col-7,
  .col-8,
  .col-9,
  .col-10,
  .col-11,
  .col-12 {
    padding-right: 5px;
    padding-left: 5px;
  }
}